<template>
  <svg
    :class="className"
    v-bind="attributes"
    v-html="svgInner"
  />
</template>

<script>
import { parse } from 'svg-parser'

export default {
  name: 'SvgIcon',
  props: {
    iconName: {
      type: String,
      required: true
    },
    className: {
      type: String
    }
  },
  data: () => ({
    svgString: '',
    attributes: {},
    svgInner: ''
  }),
  computed: {
    filepath () {
      return require(`@/assets/icons/${this.iconName}.svg`)
    }
  },
  created () {
    this.loadFile()
  },
  methods: {
    loadFile () {
      fetch(this.filepath)
        .then(response => response.text())
        .then(svg => {
          this.svgString = svg
          this.getSvgAttrs()
          this.getSvgInner()
        })
    },
    getSvgAttrs () {
      const parsedSvg = parse(this.svgString)

      const parsedSvgProps = parsedSvg.children[0].properties
      for (const key in parsedSvgProps) {
        this.attributes[key] = parsedSvgProps[key]
      }
    },
    getSvgInner () {
      const startIndex = this.svgString.indexOf('>')
      const endIndex = this.svgString.indexOf('</svg')
      this.svgInner = this.svgString.slice(startIndex + 1, endIndex)
    }
  }
}
</script>
