<template>
  <section class="main-menu">
    <div class="main-menu__container">
      <h1 class="main-menu__heading">Новый проект</h1>
      <ul class="main-menu__list">
        <li class="main-menu__item">
          <label class="main-menu__item-label" for="oss">Оператор:</label>
          <select class="main-menu__item-input input" name="oss" id="oss" v-model="chosenOss">
            <option
              v-for="operator in oss"
              :value="operator.value"
              :key="operator.id"
            >
              {{ operator.name }}
            </option>
          </select>
        </li>
        <li class="main-menu__item">
          <label class="main-menu__item-label" for="service-name">Сервис:</label>
          <select class="main-menu__item-input input" name="service-name" id="service-name" v-model="chosenService">
            <option
              v-for="service in services[chosenOss]"
              :value="service.value"
              :key="service.id"
            >
              {{ service.name }}
            </option>
          </select>
        </li>
      </ul>
      <button
        class="main-menu__start-btn btn btn--wide"
        type="button"
        @click="startBuild"
        :disabled="!chosenOss || !chosenService"
      >
        Начать
      </button>
    </div>
  </section>
</template>

<script>
export default {
  name: 'MainMenu',
  data () {
    return {
      chosenOss: 'beeline',
      chosenService: '',
      oss: this.$store.state.oss,
      services: this.$store.state.services
    }
  },
  methods: {
    startBuild () {
      this.$store.commit('setOss', this.chosenOss)
      this.$store.commit('setService', this.chosenService)
      this.$router.push('/workspace')
    }
  },
  watch: {
    chosenOss () {
      this.chosenService = ''
    }
  }
}
</script>

<style scoped>
  .main-menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
  }

  .main-menu__container {
    width: 320px;
  }

  .main-menu__heading {
    margin: 0 0 20px;
    font-size: 40px;
  }

  .main-menu__list {
    padding: 0;
    margin: 0 0 40px;
    list-style: none;
  }

  .main-menu__item {
    display: flex;
    flex-direction: column;
  }

  .main-menu__item + .main-menu__item {
    margin-top: 20px;
  }

  .main-menu__item-label {
    padding-bottom: 7px;
    font-size: 16px;
  }
</style>
