import cssGroup from '@/assets/propertiesDesignLite.json'
import store from '../store/index'

export function parseStyles (dataSelector) {
  const compStyles = {}
  const stylesList = Object.keys(cssGroup)
  for (let k = 0; k < stylesList.length; k++) {
    const styleName = stylesList[k]
    const styleValue = getComputedStyle(document.querySelector(dataSelector))[styleName]
    compStyles[styleName] = styleValue
  }
  store.commit('setComponentStyles', {
    compStyles,
    dataSelector
  })
}
