<template>
  <ul class="tabs">
    <li class="tabs__item" v-for="(value, key) in tabs" :key="Math.random() * value.length">
      <label
        :class="['tabs__item-btn', checkedTab === key ? 'tabs__item-btn--active' : '']"
        :for="`tab-${key}`"
      >
        {{ value }}
      </label>
      <input
        class="visuallyhidden"
        type="radio"
        :id="`tab-${key}`"
        :value="key"
        v-model="checkedTab"
        @change="updateStructure"
      >
    </li>
  </ul>
</template>

<script>
import eventbus from '@/utils/eventbus'

export default {
  name: 'Tabs',
  data () {
    return {
      checkedTab: this.$store.state.activeTab,
      tabs: {
        components: 'Компоненты',
        styles: 'Стили',
        export: 'Экспорт'
      }
    }
  },
  watch: {
    checkedTab () {
      this.$emit('toggleComponent', this.checkedTab)
      this.$store.commit('setActiveTab', this.checkedTab)
    }
  },
  methods: {
    updateStructure () {
      this.$nextTick(() => {
        if (this.checkedTab === 'styles') {
          eventbus.$emit('updateStructure')
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .tabs {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .tabs__item {
    flex-grow: 1;
    font-size: 22px;
    text-align: center;
  }

  .tabs__item-btn {
    display: block;
    width: 100%;
    padding: 15px 20px;
    background-color: transparent;
    border: none;
    border-bottom: 5px solid #ccc;
    cursor: pointer;
    transition: border-bottom-color 0.1s ease-in, color 0.1s ease-in;

    &:not(.tabs__item-btn--active):hover {
      border-bottom-color: $main-red;
    }

    &--active {
      color: $main-red;
      border-bottom-color: transparent;
    }
  }

  .tabs__item + .tabs__item {
    border-left: 1px solid #ccc;
  }
</style>
