<template>
  <div class="preview">
    <div class="preview__controls">
      <button class="preview__refresh-btn" @click="refreshPreview">
        <svg-icon className="preview__refresh-btn-icon" iconName="refresh" />
        Отчистить
      </button>
    </div>
    <div class="preview-window">
      <draggable
        class="list-group list-group__header"
        :class="{'placeholder placeholder--header': !placeholders.header}"
        group="components"
        v-model="$store.state.headerList"
        @change="updateLpStructure"
      >
        <component
          v-for="(comp, index) in $store.state.headerList"
          :is="`${comp}`"
          :key="Math.random() * comp.length"
          v-bind="{index, list: 'headerList'}"
          @click.ctrl.native="removeComp(index, 'headerList')"
        />
      </draggable>
      <draggable
        class="list-group list-group__main main"
        :class="{'placeholder placeholder--main': !placeholders.main}"
        group="components"
        v-model="$store.state.mainList"
        @change="updateLpStructure"
      >
        <component
          v-for="(comp, index) in $store.state.mainList"
          :is="`${comp}`"
          :key="Math.random() * comp.length"
          v-bind="{index, list: 'mainList'}"
          @click.ctrl.native="removeComp(index, 'mainList')"
        />
      </draggable>
      <draggable
        class="list-group list-group__footer"
        :class="{'placeholder placeholder--footer': !placeholders.footer}"
        group="components"
        v-model="$store.state.footerList"
        @change="updateLpStructure"
      >
        <component
          v-for="(comp, index) in $store.state.footerList"
          :is="`${comp}`"
          :key="Math.random() * comp.length"
          v-bind="{index, list: 'footerList'}"
          @click.ctrl.native="removeComp(index, 'footerList')"
        />
      </draggable>
    </div>
  </div>
</template>

<script>
import eventbus from '@/utils/eventbus'
import draggable from 'vuedraggable'
import SvgIcon from '@/components/SvgIcon'

export default {
  name: 'Preview',
  computed: {
    placeholders () {
      return {
        header: this.$store.state.headerList.length,
        main: this.$store.state.mainList.length,
        footer: this.$store.state.footerList.length
      }
    }
  },
  methods: {
    removeAt (index) {
      this.$store.commit('removeItemFromList', index)
    },
    refreshPreview () {
      this.$store.commit('refreshPreview')
      this.$store.commit('updateLpStructure')
    },
    removeComp (index, list) {
      this.$store.commit('removeItemFromList', {
        index,
        list
      })
      if (this.$store.state.activeTab === 'styles') {
        this.$store.commit('updateLpStructure')
      }
    },
    toggleTextEditable (toggler) {
      const allTextNodes = this.getAllTextNodes(document.querySelector('.preview-window'))

      allTextNodes.forEach(item => {
        item.setAttribute('contenteditable', toggler)
      })
    },
    getAllTextNodes (node) {
      var all = []
      for (node = node.firstChild; node; node = node.nextSibling) {
        if (node.nodeType === 3) all.push(node.parentNode)
        else all = all.concat(this.getAllTextNodes(node))
      }
      return all
    },
    updateLpStructure () {
      this.toggleTextEditable('true')
      this.$store.commit('updateLpStructure')
    }
  },
  mounted () {
    eventbus.$on('toggleEditable', () => {
      this.toggleTextEditable('false')
    })
  },
  components: {
    draggable,
    'svg-icon': SvgIcon
  }
}
</script>

<style scoped lang="scss">
  .preview {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }

  .preview__controls {
    position: absolute;
    top: 40px;
    left: 40px;
    right: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .preview__refresh-btn {
    width: 40px;
    height: 44px;
    padding: 0;
    font-size: 0;
    line-height: 0;
    background-color: transparent;
    border: none;

    &:hover {
      fill: $main-red;
      animation: rotate 1s infinite cubic-bezier(.52,-0.23,.52,1.23);
    }
  }

  @keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(-180deg);
    }
  }

  .preview__toggle-orientation {
    padding: 10px 20px;
    margin: 0 0 0 20px;
    font-size: 20px;
    background-color: transparent;
    border: 2px solid #333;

    &:hover {
      background-color: $main-red;
      color: #fff;
      border-color: $main-red;
    }
  }

  .preview-window {
    position: relative;
    box-sizing: content-box;
    display: grid;
    grid-template-rows: min-content 1fr min-content;
    width: 320px;
    min-width: 320px;
    height: 640px;
    min-height: 320px;
    border: 3px solid #aaa;
    overflow: hidden;
    resize: both;
  }

  .list-group {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
    min-height: 50px;
  }

  .placeholder {
    &::after {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      display: flex;
      justify-content: center;
      width: 100%;
      height: 100%;
      font-size: 40px;
      line-height: 1;
      font-weight: 700;
      color: #eee;
    }

    &--header::after {
      content: 'HEADER';
      align-items: flex-start;
    }

    &--main::after {
      content: 'MAIN';
      align-items: center;
    }

    &--footer::after {
      content: 'FOOTER';
      align-items: flex-end;
    }
  }
</style>
