import Vue from 'vue'
import Vuex from 'vuex'
import components from '../assets/components.json'
import { createLPStructure } from '@/utils/functions'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    components: [...Object.values(components)],
    activeTab: 'components',
    chosenOss: 'beeline',
    chosenService: '',
    activeElemSelector: null,
    activeElemStyles: null,
    colorPickers: {},
    oss: [
      { name: 'Билайн', value: 'beeline', id: 1000 },
      { name: 'Темафон', value: 'temafon', id: 2000 },
      { name: 'Теле2', value: 'tele2', id: 3000 },
      { name: 'Теле2(ТМедиа)', value: 'tMedia', id: 5000 }
    ],
    services: {
      beeline: [
        { name: 'Музыкальная Волна', value: 'muzvolna', id: 1001 },
        { name: 'Караоке', value: 'karaoke', id: 1002 }
      ],
      temafon: [
        { name: 'Обучающие Курсы', value: 'courses', id: 2001 },
        { name: 'Эролайф, Эромикс, Видеомир', value: 'adult', id: 2002 }
      ]
    },
    mainList: [],
    headerList: [],
    footerList: []
  },
  mutations: {
    setOss (state, payload) {
      state.chosenOss = payload
    },
    setService (state, payload) {
      state.chosenService = payload
    },
    removeItemFromList (state, payload) {
      state[payload.list].splice([payload.index], 1)
    },
    refreshPreview (state) {
      for (const list of [state.headerList, state.mainList, state.footerList]) {
        list.splice(0, list.length)
      }
    },
    updateLpStructure (state) {
      if (state.activeTab === 'styles') {
        Vue.nextTick(() => {
          createLPStructure(document.querySelector('.preview-window'), document.querySelector('.structure'), true)
        })
      }
    },
    setActiveTab (state, payload) {
      state.activeTab = payload
    },
    setComponentStyles (state, payload) {
      state.activeElemStyles = payload.compStyles
      state.activeElemSelector = payload.dataSelector
    },
    addColorPicker (state, payload) {
      Vue.set(state.colorPickers, payload.name, payload.obj)
    }
  },
  actions: {
  },
  modules: {
  }
})
