import store from '../store/index'

export function updateLocalStyles (table, newStyles) {
  for (let i = 0; i < table.rows.length; i++) {
    const row = table.rows[i]
    const cellName = row.cells[0].innerHTML.trim()

    switch (cellName) {
      case 'color':
      case 'fill':
      case 'background-color':
      case 'border-top-color':
      case 'border-left-color':
      case 'border-right-color':
      case 'border-bottom-color':
        store.state.colorPickers[cellName].setColor(newStyles[cellName])
        break

      case 'text-align':
      case 'text-transform':
      case 'border-top-style':
      case 'border-left-style':
      case 'border-right-style':
      case 'border-bottom-style':
        row.cells[1].children[0].value = newStyles[cellName]
        break

      case 'background-image':
      case 'background-position':
      case 'background-size':
        row.cells[1].children[0].value = newStyles[cellName]
        break

      case 'letter-spacing':
      case 'border-top-width':
      case 'border-left-width':
      case 'border-right-width':
      case 'border-bottom-width':
      case 'border-top-left-radius':
      case 'border-top-right-radius':
      case 'border-bottom-left-radius':
      case 'border-bottom-right-radius':
        if (parseFloat(newStyles[cellName]) || parseFloat(newStyles[cellName]) === 0) {
          row.cells[1].children[0].children[0].type = 'number'
          row.cells[1].children[0].children[0].value = parseFloat(newStyles[cellName])
        } else {
          row.cells[1].children[0].children[0].type = 'text'
          row.cells[1].children[0].children[1].disabled = true
          row.cells[1].children[0].children[0].value = newStyles[cellName]
        }
        break
    }
  }
}
