<template>
  <div class="controls">
    <v-tabs @toggleComponent="setActiveComp($event)"/>
    <div class="controls__content">
      <keep-alive>
        <component :is="activeComp"></component>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import Tabs from '@/components/tabs/Tabs'
import TabComponents from '@/components/tabs/TabComponents'
import TabStyles from '@/components/tabs/TabStyles'
import TabExport from '@/components/tabs/TabExport'

export default {
  name: 'Controls',
  data () {
    return {
      activeComp: 'v-tab-components'
    }
  },
  components: {
    'v-tabs': Tabs,
    'v-tab-components': TabComponents,
    'v-tab-styles': TabStyles,
    'v-tab-export': TabExport
  },
  methods: {
    setActiveComp (activeComp) {
      this.activeComp = `v-tab-${activeComp}`
    }
  }
}
</script>

<style scoped lang="scss">
  .controls {
    display: flex;
    flex-direction: column;
    height: 100vh;
    border-left: 5px solid #ccc;
    overflow-y: auto;
  }

  .controls__content {
    flex-grow: 1;
    padding: 20px;
  }
</style>
