import '@simonwep/pickr/dist/themes/monolith.min.css'
import Pickr from '@simonwep/pickr'
import store from '../store/index'

export function initColorPickers () {
  const colorPickerBtns = Array.from(document.querySelectorAll('.color-picker'))
  for (let i = 0; i < colorPickerBtns.length; i++) {
    const name = colorPickerBtns[i].parentElement.parentElement.children[0].innerHTML.trim()

    const pickr = Pickr.create({
      el: '.color-picker',
      theme: 'monolith',
      components: {
        // Main components
        preview: true,
        opacity: true,
        hue: true,

        // Input / output Options
        interaction: {
          hex: true,
          rgba: true,
          input: true,
          cancel: true,
          save: true
        }
      }
    })
    pickr.on('change', (color) => {
      if (store.state.activeElemSelector) {
        const elem = document.querySelector(store.state.activeElemSelector)
        elem.style[name] = color.toRGBA().toString(0)
      }
    })
    pickr.on('save', (color) => {
      if (store.state.activeElemSelector) {
        const elem = document.querySelector(store.state.activeElemSelector)
        elem.style[name] = color.toRGBA().toString(0)
      }
      pickr.hide()
    })
    pickr.on('cancel', () => {
      const elem = document.querySelector(store.state.activeElemSelector)
      elem.style[name] = pickr.getSelectedColor().toRGBA().toString(0)
      pickr.hide()
    })
    pickr.on('hide', () => {
      const elem = document.querySelector(store.state.activeElemSelector)
      elem.style[name] = pickr.getSelectedColor().toRGBA().toString(0)
      pickr.hide()
    })
    store.commit('addColorPicker', {
      name,
      obj: pickr
    })
  }
}
