import { toggleHighlighter } from '@/utils/toggleHighlighter'
import { modifySelector } from '@/utils/selectorModifier'
import { parseStyles } from '@/utils/styleParser'
let dataId = ''

export function createLPStructure (instance, elemToAppend, refresh) {
  if (refresh) {
    [...elemToAppend.children].forEach(item => {
      if (Array.from(item.classList).includes('structure__list')) {
        item.remove()
      }
    })
  }
  const list = document.createElement('ul')
  list.classList.add('structure__list')
  const li = document.createElement('li')
  li.classList.add('structure__item')

  let selectorName
  dataId = instance.dataset.id || dataId
  if (Array.from(instance.classList).includes('preview-window')) {
    selectorName = `${instance.className.split(' ')[0].toLowerCase()}`
  } else if (instance.dataset.id) {
    selectorName = `[data-id="${dataId}"]`
  } else {
    selectorName = `[data-id="${dataId}"] ${instance.className.split(' ')[0]}`.toLowerCase()
  }

  const inputId = `${instance.className.split(' ')[0]}`.toLowerCase()
  const radioLabel = document.createElement('label')
  radioLabel.classList.add('structure__label')
  radioLabel.setAttribute('for', inputId)
  const radioBtn = document.createElement('input')
  radioBtn.classList.add('visuallyhidden')
  radioBtn.classList.add('structure__radiobtn')
  radioBtn.setAttribute('type', 'radio')
  radioBtn.setAttribute('name', 'structure__radiobtn')
  const dataSelector = modifySelector(selectorName)
  radioBtn.setAttribute('data-selector', dataSelector)
  radioBtn.id = inputId
  radioBtn.addEventListener('change', () => {
    parseStyles(dataSelector)
  })

  radioLabel.append(instance.className.split(' ')[0].toLowerCase())
  li.append(radioBtn)
  li.append(radioLabel)

  li.addEventListener('mouseenter', () => {
    toggleHighlighter(selectorName, 'add')
  })
  li.addEventListener('mouseleave', () => {
    toggleHighlighter(selectorName, 'remove')
  })
  if (!selectorName.split(' ').includes('list-group')) {
    list.append(li)
  } else {
    list.classList.add('structure__list--nopadding')
  }
  elemToAppend.append(list)

  for (const child of instance.children) {
    if (
      !child.className.baseVal &&
      child.className.split(' ')[0].toLowerCase() !== 'resizer'
    ) {
      createLPStructure(child, elemToAppend.lastElementChild, false)
    }
  }
}
