import Vue from 'vue'
import VueRouter from 'vue-router'
import StartPage from '@/views/StartPage'
import Workspace from '@/views/Workspace'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'StartPage',
    component: StartPage
  },
  {
    path: '/workspace',
    name: 'Workspace',
    component: Workspace
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
