export function toggleHighlighter (selector, action) {
  let selectorDataId
  let selectorClass
  if (selector.split(' ').length > 1) {
    selectorDataId = `${selector.split(' ')[0]}`
    selectorClass = `.${selector.split(' ')[1]}`
    selector = `${selectorDataId} ${selectorClass}`
  } else if (!selector.startsWith('[')) {
    selector = `.${selector}`
  }
  if (action === 'add') {
    const elem = document.querySelector(selector)
    const pageY = elem.getBoundingClientRect().y
    const pageX = elem.getBoundingClientRect().x
    const width = elem.clientWidth
    const height = elem.clientHeight

    const hightlighter = document.createElement('div')
    hightlighter.id = 'highlighter'
    hightlighter.style.width = `${width}px`
    hightlighter.style.height = `${height}px`
    if (selector === '.preview-window') {
      hightlighter.style.top = `${pageY + 3}px`
      hightlighter.style.left = `${pageX + 3}px`
    } else {
      hightlighter.style.top = `${pageY}px`
      hightlighter.style.left = `${pageX}px`
    }

    document.body.append(hightlighter)
  } else {
    document.getElementById('highlighter').remove()
  }
}
