<template>
  <div class="tab-styles">
    <div class="structure">
      <h2 class="tab__heading">Структура LP</h2>
    </div>
    <div class="styles">
      <h2 class="tab__heading">Стили</h2>
      <table class="styles__table">
        <tr class="styles__row"
          v-for="(propValue, prop) in cssGroup"
          :key="Math.random() * prop.length"
        >
          <td class="styles__prop">{{ prop }}</td>
          <td class="styles__prop-value">
            <input class="styles__input" type="text" v-if="typeof propValue === 'string' && propValue !== 'color'">
            <button class="color-picker" type="button" v-if="propValue === 'color'">Выбрать цвет</button>
            <input class="styles__input" type="number" v-if="propValue === 0">
            <div class="styles__number-input-wrap" v-if="propValue === 1">
              <input class="styles__input styles__number-input" type="number">
              <select class="styles__unit-select styles__number-input">
                <option value="px">px</option>
                <option value="%">%</option>
                <option value="vw">vw</option>
                <option value="vh">vh</option>
              </select>
            </div>
            <select v-if="typeof propValue === 'object'">
              <option v-for="(value, valueIndex) in propValue" :value="value" :key="valueIndex">{{ value }}</option>
            </select>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>

<script>
import { createLPStructure } from '@/utils/functions'
import { initColorPickers } from '@/utils/colorPickers'
import { updateLocalStyles } from '@/utils/updateLocalStyles'
import eventbus from '@/utils/eventbus'
import cssGroup from '@/assets/propertiesDesignLite.json'

export default {
  name: 'TabStyles',
  data () {
    return {
      cssGroup
    }
  },
  methods: {
    updateStructure () {
      if (document.querySelector('.structure__list')) {
        document.querySelector('.structure__list').remove()
      }
      createLPStructure(document.querySelector('.preview-window'), document.querySelector('.structure'), true)
    }
  },
  computed: {
    activeElemStyles () {
      return this.$store.state.activeElemStyles
    }
  },
  watch: {
    activeElemStyles () {
      updateLocalStyles(document.querySelector('.styles__table'), this.activeElemStyles)
    }
  },
  mounted () {
    this.updateStructure()
    eventbus.$on('updateStructure', this.updateStructure)

    initColorPickers()
  }
}
</script>

<style lang="scss">
  .tab-styles {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-gap: 20px;
  }

  .structure {
    padding-right: 30px;
    border-right: 1px solid #ccc;
  }

  .tab__heading {
    padding-left: 10px;
    margin: 0 0 20px;
    font-size: 20px;
    font-weight: 700;
    text-decoration: underline;
  }

  .structure__item {
    display: inline-block;
  }

  .structure__label {
    display: inline-block;
    padding: 5px 10px;
    transition: background-color 0.1s ease-in, color 0.1s ease-in;

    &:hover {
      background-color: $yellowgreen;
      color: #000;
      cursor: pointer;
    }
  }

  .structure__radiobtn:checked + .structure__label {
    background-color: $main-red;
    color: #fff;
  }

  .structure__list {
    padding: 0;

    & > .structure__list {
      padding-left: 15px;
    }

    & > &--nopadding {
      padding: 0;
    }
  }

  .styles {
    padding-left: 10px;
    transition: filter 0.2s ease-in;
  }

  .styles__table {
    width: 100%;
    border-collapse: collapse;

    & + .styles__table {
      margin-top: 30px;
    }
  }

  .styles__row:not(:last-child) {
    border-bottom: 1px solid #ccc;
  }

  .styles__row:nth-child(2n) {
    background-color: #f4f4f4;
  }

  .styles__prop,
  .styles__prop-value {
    padding: 5px 10px;
  }

  .styles__prop {
    position: relative;
    width: 40%;
  }

  .styles__prop--linked {
    position: relative;
    // transform: scale(1);

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      display: block;
      background-color: rgba(255, 255, 255, .7);
    }
  }

  .styles__prop--linked .styles__prop {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      bottom: 50%;
      right: 100%;
      z-index: -1;
      display: block;
      width: 10px;
      height: 110%;
      margin-right: 5px;
      border-bottom: 1px solid rgb(31, 189, 31);
      border-left: 1px solid rgb(31, 189, 31);
    }
  }

  .styles__link-btn-box {
    position: absolute;
    top: 50%;
    right: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    margin-right: 5px;
    transform: translateY(-50%);
    font-size: 0;
    line-height: 0;
    background-color: #fff;
  }

  .styles__link-btn-icon {
    width: 20px;
    height: 20px;
    fill: #ccc;
    transition: fill 0.1s ease-in;
  }

  .styles__link-btn {
    cursor: pointer;
  }

  .styles__link-checkbox + .styles__link-btn:hover .styles__link-btn-icon {
    fill: #999;
  }

  .styles__link-checkbox:checked + .styles__link-btn .styles__link-btn-icon {
    fill: rgb(31, 189, 31);
  }

  .styles__link-checkbox:checked + .styles__link-btn:hover .styles__link-btn-icon {
    fill: rgb(29, 117, 29);
  }

  .styles__prop-value {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
  }

  .styles__input {
    width: 50%;
  }

  .styles__unit-select {
    margin-left: 5px;
  }

  .pcr-button {
    border: 1px solid #ccc;
  }
</style>
