export function modifySelector (selectorName) {
  if (!selectorName.startsWith('[')) {
    selectorName = `.${selectorName}`
  } else if (selectorName.startsWith('[') && selectorName.split(' ').length === 1) {
    selectorName = `${selectorName}`
  } else {
    selectorName = `${selectorName.split(' ')[0]} .${selectorName.split(' ')[1]}`
  }

  return selectorName
}
